*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.maindiv
{
	width: 30%;
	margin-top: 50px;
	border: 1px solid black;
	border-radius: 5px;
	background:#eee;
}

h1{
	padding-top: 20px;
	
}

.calcdiv{
	background:#521007;
	height: 40px;
	width: 100%;
	margin-top: 20px;
	margin-bottom: 20px;
}

button{
	height: 40px;
	width: 60px;
	color:white;
	border-radius: 8px;
	cursor: pointer;
	font-size: 30px;
	border:none;
	margin-bottom:20px;
	outline: none;
}

.numeric
{
	background: #4069E2;
}

.sign
{
	background: #EB4027;
}
.equal{
	background: green;
}

.ans{
	color: white;
	padding-top: 10px;
	float: right;
	margin-right: 20px;
}